$main-color: #252728;
$text-color: #c8c6cb;
$linkedin: #0a66c2;
$react: #61dafb;
$js: #efd81d;
$redux: #764abc;
$html: #e54c21;
$css: #214ce5;
$github: #f0f6fc;
$sass: #bf4080;
$next: #fff;
$tailwind: #38bdf8;
$typescript: #3178c6;

@mixin borderRadius($n) {
  border-radius: $n;
  -webkit-border-radius: $n;
  -moz-border-radius: $n;
  -ms-border-radius: $n;
  -o-border-radius: $n;
}
@mixin transform($property) {
  transform: $property;
  -webkit-transform: $property;
  -moz-transform: $property;
  -ms-transform: $property;
  -o-transform: $property;
}

@mixin transition($property, $time, $aceleration) {
  transition: $property $time $aceleration;
  -webkit-transition: $property $time $aceleration;
  -moz-transition: $property $time $aceleration;
  -ms-transition: $property $time $aceleration;
  -o-transition: $property $time $aceleration;
}
